.parallax {
    height: 90vh;
    max-height: 100vh;
    overflow: hidden;
    position: relative;
    background-position: center center;
    background-size: cover;
    margin: 0;
    padding: 0;
    border: 0;
    display: flex;
    align-items: center;
    &.filter {
        &:before {
            background: rgba(0, 0, 0, 0.5);
        }
        &:after,
        &:before {
            position: absolute;
            z-index: 1;
            width: 100%;
            height: 100%;
            display: block;
            left: 0;
            top: 0;
            content: "";
        }
    }
    &.small {
        height: 380px;
    }
}
